/**
 * function
 * @param {} cells
 * singleEdge: 1 edge
 * singleVertex: 1 vertex
 * multipleEdge:  edge
 * multipleVertex:  vertex
 * multipleAll:  edge  vertex
 */
const typeofElement = cells => {
  try {
    if (cells.length === 1) {
      return cells[0].edge ? 'singleEdge' : 'singleVertex'
    } else {
      const multipleEdge = cells.every(cell => {
        return cell.edge
      })
      const multipleVertex = cells.every(cell => {
        return !cell.edge
      })
      if (multipleEdge && !multipleVertex) {
        return 'multipleEdge'
      } else if (!multipleEdge && multipleVertex) {
        return 'multipleVertex'
      } else if (!multipleEdge && !multipleVertex) {
        return 'multipleAll'
      }
    }
  } catch (e) {
    console.log(e)
  }
}

export default {
  typeofElement
}
